.swiper-slide {
  opacity: 0.3; /* 左右のスライドを薄くする */
  transform: scale(0.8); /* 左右のスライドを小さくする */
  transition: 0.7s; /* ゆっくり小さくさせる */
  flex-shrink: 0; /* スライドの幅が自動で縮まないようにする */
  width: 100%;
}

.swiper-slide img {
  height: auto;
  width: 100%;
}

.swiper-slide-active {
  opacity: 1; /* 中央のスライドは薄くしない */
  transform: scale(1); /* 中央のスライドは小さくしない */
  z-index: 1; /* 中央のスライドを一番上にする */
}

/* 前後のスライド */
.swiper-slide-prev,
.swiper-slide-next {
  opacity: 0.3; /* 前後のスライドは透明度を30%に */
  transform: scale(0.8); /* 前後のスライドは縮小 */
}

